<template>
  <div class="common-style" :class="classData">
    <div class="header">
      <!-- <div class="header-logo header-style">
        <img src="../assets/img/logo.png" />
      </div>
      <div class="header-menu-name-info header-style">
        <img :src="menuImg" :alt="tabTitle" />
      </div>-->
      <div class="header-menu">
        <div class="date"></div>
        <ul>
          <li
            v-for="(item, i) in menus"
            :key="i"
            :class="item.active ? 'active' : ''"
          >
            <router-link
              :to="item.path"
              :key="item.id"
              @click.native="tabClick(item)"
              >{{ item.name }}</router-link
            >
          </li>
        </ul>
        <div class="header-date-info">
          <span class="time">{{ time }}</span>
          <i class="line"></i>
          <div style="display: inline-block">
            <span class="rq">{{ date.split("-").join(".") }}</span>
            <span class="day">{{ day }}</span>
          </div>
        </div>
      </div>
    </div>
    <!-- <div class="outline-line"></div> -->
    <div class="main-pages-style">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
export default {
  name: "layout",
  data() {
    return {
      menus: [


        {
          name: '远程工作站监控',
          path: '/remoteWorkMonitoring',
          logoImg: 'static/img/ycgzzjk.png',
          active: true,
          id: '1'
        },
        {
          name: '服务质量过程监控',
          path: '/serviceQualityProcess',
          logoImg: 'static/img/fwzlgcjk.png',
          active: false,
          id: '2'
        },
        {
          name: '服务指标管控',
          path: '/serviceIndexControl',
          logoImg: 'static/img/fwzbgk.png',
          active: false,
          id: '3'
        }

      ],
      menuImg: 'static/img/fwzlgcjk.png',
      tabTitle: '服务质量过程监控',
      router: '',
      date: '',   // 年月日
      dates: '',   // 服务指标管控模时间显示为上个月
      dateMonths: '',   // 服务指标管控模时间显示为上个月
      dateMonth: '',   // 年月
      time: '',
      day: '',
      tabId: '1',
      interval: null
    }
  },
  computed: {
    classData() {
      let className = 'main-style';
      window.console.log(this.tabId, 'this.tabId');
      if (this.tabId === '1') {
        className = 'control-style';
      } else if (this.tabId === '2') {
        className = 'main-style'
      } else if (this.tabId === '3') {
        className = 'remote-style';
      }

      return className
    }
  },
  watch: {},
  created() {
    this.getDqDate();
    this.interval = setInterval(() => {
      this.getDqDate();
    }, 10000)
  },
  mounted() {
    this.router = this.$route.fullPath;
    //  window.console.log('this.router',this.router);
    this.csInit();
  },
  methods: {
    // 定位信息处理
    csInit() {
      for (let item of this.menus) {
        if (item.path === this.router) {
          item.active = true;
          this.tabId = item.id;
          this.tabTitle = item.tabTitle;
          this.menuImg = item.logoImg;
        } else {
          item.active = false;
        }
      }
      if (this.router === '/') {
        this.menus[0].active = true;
        this.tabId = this.menus[0].id;
        this.tabTitle = this.menus[0].tabTitle;
        this.menuImg = this.menus[0].logoImg;
        this.$router.push({ path: this.menus[0].path });
      }
    },
    tabClick(item) {
      this.tabTitle = item.name;
      this.tabId = item.id;
      item.active = true;
      this.menuImg = item.logoImg;
      for (let sys of this.menus) {
        if (sys.id !== item.id) {
          sys.active = false
        }
      }
    },
    getDqDate() {
      let date = new Date();
      let dayMap = {
        '1': '星期一',
        '2': '星期二',
        '3': '星期三',
        '4': '星期四',
        '5': '星期五',
        '6': '星期六',
        '7': '星期天'
      };
      this.date = date.getFullYear() + '-' + (date.getMonth() < 9 ? ('0' + (date.getMonth() + 1)) : (date.getMonth() + 1)) + '-' +
        (date.getDate() < 10 ? ('0' + date.getDate()) : date.getDate());
      this.dateMonth = date.getFullYear() + '-' + (date.getMonth() < 9 ? ('0' + (date.getMonth() + 1)) : (date.getMonth() + 1));
      this.time = (date.getHours() < 10 ? ('0' + date.getHours()) : date.getHours()) + ':' +
        (date.getMinutes() < 10 ? ('0' + date.getMinutes()) : date.getMinutes());
      this.day = dayMap[date.getDay() + ''];

      //服务指标管控模时间显示为上个月
      this.dates = date.getFullYear() + '-' + (date.getMonth() < 10 ? ('0' + (date.getMonth())) : (date.getMonth())) + '-' +
        (date.getDate() < 10 ? ('0' + date.getDate()) : date.getDate());
      this.dateMonths = date.getFullYear() + '-' + (date.getMonth() < 10 ? ('0' + (date.getMonth())) : (date.getMonth()));
    }
  },
  destroyed() {
    window.clearInterval(this.interval);
  }
}
</script>

<style scoped lang="scss">
.control-style {
  background: url("../assets/img/背景1.jpg") no-repeat top center fixed;
  background-size: 100% 100%;
}
.main-style {
  background: url("../assets/img/背景2.jpg") no-repeat top center fixed;
  background-size: 100% 100%;
}

.remote-style {
  background: url("../assets/img/背景3.jpg") no-repeat top center fixed;
  background-size: 100% 100%;
}

.common-style {
  width: 100%;
  height: 100%;

  .header {
    width: 100%;
    height: 10%;
    display: flex;

    .header-style {
      float: left;
    }

    // .header-logo {
    //   width: 424 / 1920 * 100%;
    //   /* height: 150px; */
    //   padding-top: 10px;
    //   margin-left: 20px;
    //   img {
    //     width: 100%;
    //     //   // height: 56px;
    //     // object-fit: cover;
    //   }
    // }

    // .header-menu-name-info {
    //   flex: 1;
    //   // max-width: 300px;
    //   font-size: 22px;
    //   line-height: 50px;
    //   color: #fff;
    //   margin-left: 20px;
    //   margin-top: 10px;
    //   display: flex;
    //   // img {
    //   //   width: 100%;
    //   //   height: 60px;
    //   // }
    // }
    .date {
      flex: 1.2;
    }
    .header-menu {
      display: flex;
      width: 100%;
      ul {
        flex: 4;
        list-style: none;
        // left: calc(50% - 855px / 2);
        // position: absolute;
        display: flex;
        padding-top: 10px;
        li {
          display: flex;
          justify-content: center;
          align-items: center;
          // position: absolute;
          flex: 1;
          float: left;
          font-size: 22px;
          text-align: center;
          cursor: pointer;
          a {
            color: #559bf1;
            width: 100%;
            // height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            font-weight: 600;
          }
        }

        li.active {
          a {
            color: #fff;
          }
        }
      }
    }

    .header-date-info {
      display: flex;
      flex: 1.2;
      align-items: center;
      color: #fff;
      span.time {
        margin-left: calc(50% - 80px);
        display: inline-block;
        height: 50px;
        line-height: 50px;
        font-size: 36px;
        box-sizing: border-box;
      }
      span.rq {
        display: block;
        height: 25px;
        line-height: 25px;
        font-size: 20px;
      }
      span.day {
        display: block;
        height: 25px;
        line-height: 25px;
        font-size: 16px;
      }
      .line {
        width: 4px;
        height: 50px;
        line-height: 50px;
        margin: 0 30px;
        background-image: linear-gradient(#000781, #23b7cb, #000781);
      }
    }
  }
  .outline-line {
    display: block;
    width: 100%;
    height: 4px;
    background-image: linear-gradient(to right, #000781, #23b7cb, #000781);
  }
  .main-pages-style {
    width: 100%;
    height: calc(100% - 100px);
    position: relative;
  }
}
</style>